@import 'vars';
@import 'fonts';
@import 'animation';

::placeholder {
  color: #666;
}
  
::selection {
  background-color: $accent;
  color: #fff;
}
  
html {
  overflow-x: hidden;
  box-sizing: border-box;
}

*, ::before, ::after {
  box-sizing: inherit;
}
  
body{
  position: relative;
  min-width: 320px;
  overflow: hidden;
  font-weight: 400;
  font-size: 16px;
  font-family: $font-default;
  line-height: 1.4;
  color: $text;
  
  input, textarea {
    border: #666 1px solid;
    outline: none;
  }
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

b, strong {
  font-weight: bold;
}
  
.btn {
  padding: 15px;
  font-weight: 700;
  font-size: 18px;
  color: #ffffff;
  text-transform: uppercase;
  background-color: $accent;
  border: none;
  border-radius: 30px;
  transition: $slowdown;
  cursor: pointer;
  
  &:hover {
    color: #ffffff;
    text-decoration: none;
    background-color: lighten($accent, 10%);
    box-shadow: 0 2px 9px 3px rgba(0, 0, 0, 0.2);
  }
}

.link-btn {
  display: block;
  width: 250px;
  margin: 0 auto;
  
  &:hover {
    color: #ffffff;
    text-decoration: none;
  }
}
  
.no-btn {
  padding: 0;
  background: none;
  border: none;

  &:active, &:focus {
    outline: none;
  }
}

.lst {
  margin: 0;
  padding: 0;
  list-style: none;
}
   
h1, h2, h3, h4 {
  margin-top: 0;
  font-weight: 700;
  line-height: 1.2;
}

p {
  margin-top: 0;
}
  
.title-text {
  margin-bottom: 0;
  font-weight: 700;
  font-size: 24px;
  text-align: center;
  text-transform: uppercase;
  line-height: 1.2;
}
  
.text-accent {
  color: $accent;
}

.text-black {
  font-weight: 900;
}

.text-up {
  text-transform: uppercase;
}
  
.text-nowrap {
  white-space: nowrap;
}
  
.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 15px;
}

